:root {
  --black: #111;
  --grey: #1e1823;
  --orange: #fd9222;
  --bright-orange: #fcf1e0;
  --white: white;
  --tiny-p: #111111b3;
  --background-color: #fafafa;
  --font: "Montserrat", sans-serif;
}

body {
  background-color: var(--background-color);
  font-family: Montserrat, sans-serif;
}

button {
  cursor: pointer;
}

a {
  cursor: pointer;
  text-decoration: none;
}

li {
  list-style-type: none;
}

* {
  margin: 0;
  padding: 0;
}

@media (min-width: 375px) {
  .header_networks, .header_nav-list {
    display: none;
  }

  .header-link {
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
  }

  .header-link svg {
    transition: stroke .25s cubic-bezier(.4, 0, .2, 1);
  }

  .header-link:hover {
    color: var(--orange);
  }

  .header-link svg {
    stroke: var(--black);
  }

  .header-link svg:hover {
    stroke: var(--orange);
  }

  button {
    background-color: #0000;
    border: none;
  }

  .header {
    justify-content: center;
    display: flex;
  }

  .header-container {
    width: 335px;
    padding-top: 24px;
    padding-bottom: 32px;
  }

  .header_nav {
    width: 100%;
    justify-content: space-between;
    display: flex;
  }

  .header-logo .logospan {
    font-style: normal;
  }

  .header-logo {
    color: var(--black);
    letter-spacing: -.42px;
    align-items: center;
    gap: 4px;
    font-family: Montserrat;
    font-size: 14px;
    font-style: italic;
    font-weight: 700;
    line-height: 18px;
    display: flex;
  }

  .menu-container {
    width: 220px;
    height: 100vh;
    background-color: var(--grey);
    flex-direction: column;
    justify-content: space-between;
    padding: 20px;
    transition: transform .25s ease-in-out;
    display: flex;
    position: fixed;
    top: 0;
    right: 0;
    transform: translateX(100%);
  }

  .menu-container.is-open {
    transform: translateX(0);
  }

  .menu-container .menu-toggle {
    color: var(--white);
    position: absolute;
    top: 16px;
    right: 16px;
  }

  .menu-toggle svg {
    stroke: var(--white);
  }

  .menu-toggle svg:hover {
    stroke: var(--orange);
  }

  .mobile-menu {
    flex-direction: column;
    gap: 24px;
    margin-top: 136px;
    margin-left: 30px;
    display: flex;
  }

  .mobile-menu .header-link {
    color: var(--white);
    letter-spacing: -.28px;
    text-align: left;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 16px;
  }

  .mobile-menu .header-link:hover, .mobile-menu .active {
    color: var(--orange);
  }

  .header_networks-mobile {
    gap: 18px;
    display: flex;
  }
}

@media (min-width: 768px) and (max-width: 1199px) {
  .header-container {
    width: 704px;
  }

  button svg {
    height: 32px;
    width: 32px;
  }

  .menu-container {
    width: 290px;
    padding: 24px 32px;
  }

  .mobile-menu {
    margin-left: 53px;
  }
}

@media (min-width: 1200px) {
  .header-container {
    width: 1136px;
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  .header-logo {
    letter-spacing: -.54px;
    font-size: 18px;
    line-height: 20px;
  }

  .header_nav {
    width: 80%;
    align-items: center;
    display: flex;
  }

  .header_nav-list {
    gap: 24px;
    display: flex;
  }

  .header_nav-list a {
    color: var(--black);
    letter-spacing: -.28px;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 16px;
  }

  .header_nav button {
    display: none;
  }

  .header_networks {
    width: 66px;
    align-items: center;
    gap: 18px;
    display: flex;
  }
}





















